body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
  background-color: #fff !important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387;
}
.navbar a{
  font-size: 1.3rem;
  text-transform:capitalize;

}
.navbar a:hover{
  color: #3498db;
  text-decoration: none;
}
.menu_active{
  font-weight: bold;
  
}

.img1{
  height: 70vh;
  width: 550px;
  margin-top: 1px;
 
  
  
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header .brand-name{
  color: #3498db;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 500;
  color: black;
}
#header .btn-get-starting{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-starting:hover{
  background-color: #3498db;
  color: white;
}